import back from '../../components/back.vue';
export default {
	data() {
		return {
			phoneNum:'',
			pageNameFlag:1,
			showFlag:0,
			pageName:'忘记密码',
			text:'验证码将发送到绑定手机号',
			vercode:'',
			resetFlag:0,
			nextFlag:0,
			sendAgain:'发送验证码',
			sendAgainFlag:0,
			second:60,
			errorMsg:'',
			loadingFlag:false,
			disabledFlag:false
		}
	},
	methods: {
		changeVercode(e){
			var that = this;
			if(e.detail.value.length>0){
				that.vercode=e.detail.value;
				that.resetFlag=1;
			}else{
				that.vercode='';
				that.resetFlag=0;
			}
			if(e.detail.value.length==6){
				that.nextFlag=1
			}else{
				that.nextFlag=0
			}
		},
		sendVercodeAgain(){
			var that = this;
			that.sendAgainFlag=1
			uni.request({
			    url: that.serverUrl+'/user/authCode', 
				method:'POST',
			    data: {
			       phone:that.phoneNumTrue,
				   devCode:that.cid(),
				   type:3
			    },
			    header: {
			        'content-type': 'application/x-www-form-urlencoded'
			    },
				sslVerify:false,
			    success: (res) => {
					if(res.data.resultCode=='10000'){
						
					}else{
						that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
						that.showFlag=1
						setTimeout(function() {
							that.errorMsg='';
							that.showFlag=0;
						}, 5000);
					}
			    },
				fail() {
					uni.showModal({
					    title: '提示',
					    content: '网络异常，请检查网络后重试',
						showCancel:false,
					    success: function (res) {
					        if (res.confirm) {
					            console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
			});
			if(that.second!=60){
				return;
			}
			that.second--;
			var intervalID =setInterval(function(){
				that.sendAgain ='重新发送('+that.second-- +"S)";
			},1000);
			setTimeout(function(){
				clearInterval(intervalID);
				that.sendAgain ="发送验证码";
				that.second=60;
				that.sendAgainFlag=0
			},61000)
		},
		reset(){
			this.vercode='';
			this.resetFlag=0;
			this.nextFlag=0
		},
		nextPage(){
			var that= this;
			that.disabledFlag=true;
			that.loadingFlag=true;
			uni.request({
			    url: that.serverUrl+'/user/register', 
				method:'POST',
			    data: {
			       phone:that.phoneNumTrue,
				   devCode:that.cid(),
				   code:that.vercode
			    },
			    header: {
			        'content-type': 'application/x-www-form-urlencoded'
			    },
				sslVerify:false,
			    success: (res) => {
					that.loadingFlag=false;
					that.disabledFlag=false;
					if(res.data.resultCode=='10000'){
						var userInfo = eval(res.data.data);
						userInfo.phoneNum=that.phoneNumTrue;
						uni.setStorageSync('userInfo',userInfo);
						uni.navigateTo({
							url:'../setNewPassword/fastLoginSetPassword?phoneNum='+that.phoneNumTrue
						})
					}else{
						that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
						that.showFlag=1
						setTimeout(function() {
							that.errorMsg='';
							that.showFlag=0;
						}, 5000);
					}
			    },
				fail:(re)=>{
					that.loadingFlag=false;
					that.disabledFlag=false;
					uni.showModal({
					    title: '提示',
					    content: '网络异常，请检查网络后重试',
						showCancel:false,
					    success: function (res) {
					        if (res.confirm) {
					            console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
			});
		
			
			
			
			uni.navigateTo({
				url:'../setNewPassword/setNewPassword?phoneNum='+this.phoneNum
			})
		}
	},
	onLoad(e) {
		this.phoneNumTrue = e.phoneNum;
		this.phoneNum = e.phoneNum.substr(0,3)+'****'+e.phoneNum.substr(7,11);
	},
	components:{
		back
	}
}
